body {
  background: #FFF;
  line-height: 1.4;
  font-size: $fz;
  @include light;
  color: $c-text;
  @include no-scroll;
  @include sm {
    font-size: $fz-sm; } }

input, textarea {
  font-family: inherit;
  @include placeholder {
    font-family: inherit; } }

.overflow-visible {
  overflow: visible !important; }

.icon {
  display: inline-block;
  fill: currentColor;
  font-size: inherit; }

// [data-loaded="true"]
//   animation: fadeIn 1s

.container {
  max-width: 1440px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 15px;
  padding-right: 15px;
  &--sm {
    @include r(1699) {
      max-width: 1200px; }
    @include xl {
      max-width: 1000px; } } }

.logo {
  $this: &;
  color: $white;
  @include extrabold;
  text-transform: uppercase;
  font-size: $fz-md;
  // max-width: 150px
  @include flex(flex-start, center);
  line-height: 0.95;
  @include xs {
    font-size: $fz;
    img {
        // max-width: 75%
        height: 28px;
        width: 28px; } }
  @include r(389) {
    font-size: $fz-sm;
    img {
      height: 22px;
      width: 22px; } }

  &__img {
    margin-right: 12px;
    @include sm {
      margin-right: 5px; }
    @include r(389) {
      margin-right: 2px; } }
  &__text {
    span {
      display: block; } }
  &--color {
    #{$this}__text {
      span {
        &:first-child {
          color: $accent; }
        &:last-child {
          color: #66656a; } } } } }

.link-arrow {
  color: $white;
  position: relative;
  @include inline-flex(flex-start, center);
  @include hover {
    .link-arrow__arrow {
      width: 70px; } }
  &__title {
    margin-right: 20px; }
  &__arrow {
    position: relative;
    width: 50px;
    right: 0;
    top: 50%;
    transform: translate(0, -50%);
    display: inline-block;
    transition: transform .3s, width .3s;
    &::before {
      width: 100%;
      content: '';
      position: absolute;
      height: 1px;
      background-color: currentColor;
      top: 50%;
      right: -1px;
      transform: translate(0, -50%);
      transition: width .3s; }
    &::after {
      display: inline-block;
      position: absolute;
      @include icon-arrow(8, 8, 1, currentColor, 225);
      content: '';
      top: 50%;
      transform: translate(0, -50%) rotate(225deg);
      right: 0; } } }

.text-stroke {
  // font-size: 250px
  line-height: .9;
  text-transform: uppercase;
  @include bold;
  -webkit-text-stroke: 1px $gray-dark;
  color: transparent;
  @include css-lock(100, 250, 320, 1199, font-size); }

.purple-text {
  color: $purple;
  @include bold;
  text-shadow: 0 0 50px rgba($purple, 1), 3px 15px 2px rgba($black-darkest, .4), rgba($white, .8) 0px -1px 0px; }

.scroll-down {
  $this: &;
  color: $white;
  position: relative;
  padding-bottom: 50px;
  display: inline-block;
  @include hover {
    #{$this}__arrow {
      height: 60px; } }
  &__arrow {
    height: 50px;
    display: inline-block;
    position: absolute;
    left: 50%;
    top: 1.5em;
    transform: translate(-50%, 0);
    @include tr(height .3s);
    @include before {
      height: 100%;
      width: 1px;
      background-color: currentColor;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-50%, 0); }
    @include after {
      @include icon-arrow(10, 10, 1, currentColor, -45);
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translate(-50%, 0) rotate(-45deg); } } }

.scroll-top {
  $this: &;
  display: inline-block;
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-right: 30px;
  @include hover {
    #{$this}__arrow {
      height: 40px; } }
  &__arrow {
    height: 30px;
    width: 10px;
    display: inline-block;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translate(0, -50%);
    vertical-align: middle;
    @include tr(height .3s);

    @include before {
      height: 100%;
      background-color: currentColor;
      width: 1px;
      position: absolute;
      left: 50%;
      top: 0;
      transform: translate(-50%, 0); }
    @include after {
      @include icon-arrow(10, 10, 1, currentColor, 135);
      position: absolute;
      top: 0;
      left: 50%;
      transform: translate(-50%, 0) rotate(135deg); } } }


// ========================= animations ==============================

.glowing {
  animation: glow 2s infinite; }

.flashIn {
  animation-duration: 1.2s;
  &.is-animate {
    animation-name: flashIn; } }

@keyframes glow {
  0% {
    opacity: .5; }
  50% {
    opacity: 1; }
  100% {
