.section-bg {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  @include cover;
  opacity: 0; }

.section {
  @include cover;
  @include flex(center, normal, column);
  height: 100vh;
  overflow: hidden;
  position: relative;
  background-color: $black-darkest;
  padding: $header-height 0;
  @include md {
    padding: $header-height-md 0; }
  @include sm {
    padding: $header-height-sm 0; }

  // if fixed footer
  // padding: $header-height 0 $footer-height
  // +lg
  //   padding: $header-height 0 $footer-height-lg
  // +md
  //   padding: $header-height-md 0 $footer-height-md
  // +sm
  //   padding: $header-height-sm 0
  // +xs
  //   padding: $header-height-sm 0 $footer-height-xs

  &:last-child {
    padding: $header-height-sm 0 0; }
  &:nth-child(even) {
    background-color: $black-darken; }
  &:nth-child(odd) {
    background-color: $black-light; }
  &.ps--active-y {
    justify-content: flex-start; }
  &__block {
    width: 100%;
    flex-grow: 1;
    @include flex(center, normal, column);
    opacity: 0;
    .container {
      width: 100%; } }
  &__word {
    position: absolute;
    white-space: nowrap;
    top: 20%;
    left: -20%; }
  &__word-bottom {
    @extend .section__word;
    left: 30%;
    top: auto;
    bottom: 10%; }
  &__scroll-down {
    position: absolute;
    bottom: 35px;
    left: 50%;
    transform: translate(-50%, 0); }
  &__float-img {
    position: absolute;
    top: 50%;
    right: 0;
    z-index: 2;
    pointer-events: none;
    @include r(1799) {
      display: none; } }
  &__right {
    width: 50%;
    margin-left: 50%;
    @include md {
      width: 60%;
      margin-left: 40%; }
    @include sm {
      width: 70%;
      margin-left: 30%; }
    @include xs {
      width: auto;
      margin-left: 0; } }
  &__center {
    max-width: 750px;
    margin-left: auto;
    margin-right: auto; }


  &--center {
    text-align: center;
    justify-content: center; }
  &--no-pad {
    padding: 0 !important; }
  &--has-nav {
    .section__block {
      @include r(1799) {
        padding-left: 150px; }
      @include md {
        padding-left: 100px; }
      @include sm {
        padding-left: 50px; }
      @include xs {
        padding-left: 0; } } } }

.section-block {
  &__title {
    margin-bottom: 60px;
    @include lg {
      margin-bottom: 50px; }
    @include md {
      margin-bottom: 40px; }
    @include sm {
      margin-bottom: 30px; } }
  &__descr-text {
    position: relative;
    border-left: 1px solid currentColor;
    padding: 15px;
    margin-bottom: 50px;
    max-width: 380px;
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } }
  &__descr-list {
    margin-bottom: 50px;
    @include lg {
      margin-bottom: 40px; }
    @include md {
      margin-bottom: 30px; }
    @include sm {
      margin-bottom: 20px; } }
  &__blocks {
    @include flex(flex-start, normal, row, wrap);
    .block {
      @include notlast {
        margin-right: 10%; } } } }

.section-list {
  @extend .text;
  li {
    position: relative;
    padding-left: 20px;
    margin-bottom: 35px;
    @include lg {
      margin-bottom: 25px; }
    @include md {
      margin-bottom: 15px; }
    @include sm {
      margin-bottom: 10px; } }
  &__icon {
    display: inline-block;
    border: 1px solid $accent;
    @include inline-flex(center, center);
    position: absolute;
    left: 0;
    top: .3em;
    height: 14px;
    width: 14px;
    border-radius: 50%;
    @include before {
      background-color: $accent;
      border-radius: 50%;
      height: 6px;
      width: 6px; } } }

